import React from "react";
import _ from "lodash";

export default class TodoCount extends React.Component {
  //   

  renderTasksCount() {
    const tasksCount = _.filter(this.props.todos, o => {
      return !o.iscompleted;
    }).length;
    return "  : " + tasksCount;
  }

  render() {
    return <p>{this.renderTasksCount()}</p>;
  }
}
